*, ::after, ::before {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

/* width */
::-webkit-scrollbar 
{
  background: rgb(200 200 200);
  width: 8px;
}
/* Track */
::-webkit-scrollbar-track 
{
  border-radius: 15px;
}
 /* Handle */
::-webkit-scrollbar-thumb 
{
  background:#008060;
  border-radius: 15px;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover 
{
  background:#008060;
}
.header {
    position: fixed;
    width: 100%;
    z-index: 1000;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0 10px 100px rgb(0 0 0 / 10%);
}

.header__logo-img-cont {
    width: 5rem;
    height: 5rem;
    border-radius: 50px;
    overflow: hidden;
    margin-right: 1.5rem;
}

.header__logo-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    display: block;
}

.header__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 5rem;
}

.header__logo-container {
    display: flex;
    align-items: center;
    cursor: pointer;
    color: #333;
    transition: color .3s;
}

.header__logo-content {
    font-size: 1.8rem;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 1px;
}

.header__links {
    display: flex;
}

.header__link-wrapper {
    list-style: none;
}

.header__link {
    padding: 2.2rem 3rem;
    display: inline-block;
    font-size: 1.6rem;
    color: #333;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 700;
    transition: color .3s;
}

.header__main-ham-menu-content {
    cursor: pointer;
    display: none;
    width: 3rem;
    padding: 2.2rem 0;
}


.d-none {
    display: none;
}

.header__main-ham-menu {
    width: 100%;
}

.header__main-ham-menu:hover {
    -webkit-filter: brightness(2);
            filter: brightness(2);
}

.header__main-ham-menu-close {
    width: 100%;
}

.header__main-ham-menu-close:hover {
    -webkit-filter: brightness(2);
            filter: brightness(2);
}

.header__sm-menu {
    background: #fff;
    position: absolute;
    width: 100%;
    top: 100%;
    left: 0;
    visibility: hidden;
    opacity: 0;
    transition: all .3s;
    box-shadow: 5px 5px 0 rgb(0 0 0 / 10%);
}


.header__sm-menu-link:first-child a {
    border-top: 1px solid #eee;
}


.header__sm-menu-link a {
    display: block;
    padding: 2.5rem 3rem;
    font-size: 1.6rem;
    color: #333;
    text-align: right;
    border-bottom: 1px solid #eee;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: color .3s;
}

.header__sm-menu-link a:hover {
    color: #008060
}

.header__link:hover {
    color: #008060
}

.header__logo-content:hover {
    color: #008060
}
.home-hero {
    color: #fff;
    background:linear-gradient(to right,rgba(0,128,96,.8),rgba(0,128,96,.8)), url('https://d33wubrfki0l68.cloudfront.net/daac8858fd4a0ccea44f59dfd079c3c16c263f33/c157c/assets/svg/common-bg.svg');
    background-size: 100%;
    height: 100vh;
    min-height: 80rem;
    max-height: 120rem;
    position: relative;
}

.home-hero__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 90rem;
    width: 92%;
}

.heading-primary {
    font-size: 6rem;
    text-transform: uppercase;
    letter-spacing: 3px;
    text-align: center;
    text-shadow: 2px 3px 4px rgb(0 0 0 / 25%);
}

.home-hero__info {
    margin: 3rem auto 0 auto;
    max-width: 80rem;
}

.text-primary {
    color: #fff;
    font-size: 2.2rem;
    text-align: center;
    width: 100%;
    line-height: 1.6;
    text-shadow: 1px 2px 3px rgb(0 0 0 / 25%);
}

.home-hero__cta {
    margin-top: 5rem;
    text-align: center;
}

.btn--bg {
    padding: 1.5rem 8rem;
    font-size: 2rem;
}

.btn {
    background: #fff;
    color: #333;
    text-transform: uppercase;
    letter-spacing: 2px;
    display: inline-block;
    font-weight: 700;
    border-radius: 5px;
    box-shadow: 0 5px 15px 0 rgb(0 0 0 / 15%);
    transition: transform .3s;
}

.btn:hover {
    transform: translate(0, -5px);
}

.home-hero__socials {
    position: absolute;
    top: 50%;
    border: 2px solid #eee;
    transform: translateY(-50%);
}

.home-hero__social {
    width: 5rem;
}

.home-hero__social-icon {
    width: 100%;
}

.home-hero__social-icon-link {
    width: 100%;
    display: block;
    padding: 1.2rem;
    border-bottom: 2px solid #eee;
    transition: .2s ease-in-out;
}

.home-hero__social-icon-link:hover {
    background:rgb(50 50 50 / 5%);
}

.home-hero__social-icon-link--bd-none {
    border-bottom: 0;
}

.arrows {
	width: 60px;
	height: 72px;
	position: absolute;
	left: 50%;
	margin-left: -30px;
	bottom: 20px;
    cursor: pointer;
}

.arrows path {
	stroke: #f5f5f5;
	fill: transparent;
	stroke-width: 1.5px;	
	animation: arrow 2s infinite;
	-webkit-animation: arrow 2s infinite; 
}

@keyframes arrow
{
0% {opacity:0}
40% {opacity:1}
80% {opacity:0}
100% {opacity:0}
}

.arrows path.a1 {
	animation-delay:-1s;
	-webkit-animation-delay:-1s; /* Safari 和 Chrome */
}

.arrows path.a2 {
	animation-delay:-0.5s;
	-webkit-animation-delay:-0.5s; /* Safari 和 Chrome */
}

.arrows path.a3 {	
	animation-delay:0s;
	-webkit-animation-delay:0s; /* Safari 和 Chrome */
}
